
<template>
  <div class="page-container">
    <!--工具栏-->
    <div class="toolbar" style="float:left;padding-top:10px;padding-left:15px;">
      <div class="toolbar" style="float:left;padding-top:10px;padding-left:15px;">
        <el-form :inline="true" :model="filters" :size="size">
          <el-form-item>
            <el-input v-model="filters.USER_ACCOUNT" placeholder="用户名"></el-input>
          </el-form-item>
          <el-form-item>
            <kt-button
              icon="fa fa-search"
              :label="$t('action.search')"
              perms="sys:account:search"
              type="primary"
              @click="findPage(null)"
            />
          </el-form-item>
          <el-form-item>
            <kt-button
              icon="fa fa-plus"
              :label="$t('action.add')"
              perms="sys:account:add"
              type="primary"
              @click="handleAdd"
            />
          </el-form-item>
        </el-form>
      </div>
    </div>
    <!--表格内容栏-->
    <el-table :data="pageResult.content" style="width: 100%"  v-loading="loading" :element-loading-text="$t('action.loading')">
      <el-table-column
        v-for="column in filterColumns"
        header-align="center"
        align="center"
        :prop="column.prop"
        :label="column.label"
        :width="column.width"
        :min-width="column.minWidth"
        :fixed="column.fixed"
        :key="column.prop"
        :type="column.type"
        :formatter="column.formatter"
        :sortable="column.sortable==null?true:column.sortable"
      ></el-table-column>
      <el-table-column prop="V_PAY_TYPE" label="渠道类型" width="120">
        <template slot-scope="scope">
            <span v-if="scope.row.V_PAY_TYPE == '01'">支付宝</span>
            <span v-else-if="scope.row.V_PAY_TYPE == '02'">微信</span>
        </template>
      </el-table-column>
      <el-table-column prop="V_IS_MATCH" label="是否匹配" width="120">
        <template slot-scope="scope">
          <el-switch
            v-model="scope.row.V_IS_MATCH"
            active-color="#13ce66"
            active-value="Y"
            inactive-value="N"
            @change="accountMatch(scope.row)"
          ></el-switch>
        </template>
      </el-table-column>
      <el-table-column fixed="right" align="center" width="180" label="操作">
        <template slot-scope="scope">
          <kt-button
            icon="fa fa-edit"
            :label="$t('action.edit')"
            perms="sys:account:edit"
            :size="size"
            @click="handleEdit(scope.row)"
          />
          <kt-button
            icon="fa fa-trash"
            :label="$t('action.delete')"
            perms="sys:account:delete"
            :size="size"
            type="danger"
            @click="handleDelete(scope.row)"
          />
        </template>
      </el-table-column>
    </el-table>
    <!--分页栏-->
    <div class="toolbar" style="padding:10px;">
      <el-pagination
        layout="total, prev, pager, next, jumper"
        @current-change="refreshPageRequest"
        :current-page="pageResult.page"
        :page-size="pageResult.size"
        :total="pageResult.totalElements"
        style="float:right;"
      ></el-pagination>
    </div>
    <!--新增编辑界面-->
    <el-dialog
      :title="operation?'新增':'编辑'"
      width="40%"
      :visible.sync="dialogVisible"
      :close-on-click-modal="false"
    >
      <el-form
        :model="dataForm"
        label-width="80px"
        :rules="dataFormRules"
        ref="dataForm"
        :size="size"
        label-position="right"
      >
        <el-form-item label="ID" prop="ID" v-if="false">
          <el-input v-model="dataForm.ID" :disabled="true" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="账户名" prop="V_PAY_NAME">
          <el-input v-model="dataForm.V_PAY_NAME" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="支付类型" prop="V_PAY_TYPE">
          <el-select v-model="dataForm.V_PAY_TYPE" placeholder="请选择" style="width: 100%;">
            <el-option label="支付宝" value="01"></el-option>
            <el-option label="微信" value="02"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="是否匹配" prop="V_IS_MATCH">
          <el-select v-model="dataForm.V_IS_MATCH" placeholder="请选择" style="width: 100%;">
            <el-option label="是" value="Y"></el-option>
            <el-option label="否" value="N"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="图片上传" prop="V_FILE_ID">
          <el-upload
            class="avatar-uploader"
            action="/api/attach/fileUpload"
            :show-file-list="false"
            :on-success="handleAvatarSuccess"
            :before-upload="beforeAvatarUpload"
          >
            <img v-if="imageUrl" :src="imageUrl" class="avatar">
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button :size="size" @click.native="dialogVisible = false">{{$t('action.cancel')}}</el-button>
        <el-button
          :size="size"
          type="primary"
          @click.native="submitForm"
          :loading="editLoading"
        >{{$t('action.submit')}}</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import PopupTreeInput from "@/components/PopupTreeInput";
import KtTable from "@/views/Core/KtTable";
import KtButton from "@/views/Core/KtButton";
import TableColumnFilterDialog from "@/views/Core/TableColumnFilterDialog";
import { format } from "@/utils/datetime";
export default {
  components: {
    PopupTreeInput,
    KtTable,
    KtButton,
    TableColumnFilterDialog
  },
  data() {
    return {
      loading:false,
      imageUrl: "",
      size: "small",
      filters: {
        V_PAY_NAME: ""
      },
      columns: [],
      filterColumns: [],
      pageRequest: { page: 1, size: 10 },
      pageResult: {},
      operation: false, // true:新增, false:编辑
      dialogVisible: false, // 新增编辑界面是否显示
      editLoading: false,
      dataFormRules: {
        V_PAY_NAME: [
          { required: true, message: "请输入账户名", trigger: "blur" }
        ]
      },
      // 新增编辑界面数据
      dataForm: {
        ID: "",
        V_PAY_NAME: "",
        V_PAY_TYPE: "",
        V_IS_MATCH: "",
        V_FILE_ID: ""
      }
    };
  },
  methods: {
    // 获取分页数据
    findPage: function(data) {
      if (data) {
        this.pageRequest = data.pageRequest;
      }
      var searchForm = {};
      Object.assign(searchForm, this.filters, {
        page: JSON.stringify(this.pageRequest.page),
        size: JSON.stringify(this.pageRequest.size)
      });
  this.loading = true;
      this.$api.account
        .findPage(searchForm)
        .then(res => {
          this.pageResult = res.result;
          this.loading = false;
        })
        .then(data != null ? data.callback : "");
    },
    // 批量删除
    handleDelete: function(data) {
      this.$api.account.batchDelete(data).then(res => {
        this.editLoading = false;
        if (res.success) {
          this.$message({ message: "操作成功", type: "success" });
        } else {
          this.$message({
            message: "操作失败, " + res.msg,
            type: "error"
          });
        }
        this.findPage(null);
      });
    },
    // 显示新增界面
    handleAdd: function() {
      this.dialogVisible = true;
      this.operation = true;
      this.dataForm = {
        ID: "",
        V_PAY_NAME: "",
        V_PAY_TYPE: "",
        V_IS_MATCH: "",
        V_FILE_ID: ""
      };
    },
    // 显示编辑界面
    handleEdit: function(params) {
      this.dialogVisible = true;
      this.operation = false;
      this.dataForm = Object.assign({}, params);
    },
    // 编辑
    submitForm: function() {
      this.$refs.dataForm.validate(valid => {
        if (valid) {
          this.$confirm("确认提交吗？", "提示", {}).then(() => {
            this.editLoading = true;
            let params = Object.assign({}, this.dataForm);
            this.$api.account
              .save(params)
              .then(res => {
                this.imageUrl = "";
                this.dataForm.V_FILE_ID = "";
                this.editLoading = false;
                if (res.success) {
                  this.$message({ message: "操作成功", type: "success" });
                  this.dialogVisible = false;
                  this.$refs["dataForm"].resetFields();
                } else {
                  this.$message({
                    message: "操作失败, " + res.msg,
                    type: "error"
                  });
                }
                this.findPage(null);
              })
              .catch(res => {
                this.editLoading = false;
                this.$message({
                  message: res.message,
                  type: "error"
                });
              });
          });
        }
      });
    },
    // 处理表格列过滤显示
    displayFilterColumnsDialog: function() {
      this.$refs.tableColumnFilterDialog.setDialogVisible(true);
    },
    // 处理表格列过滤显示
    handleFilterColumns: function(data) {
      this.filterColumns = data.filterColumns;
      this.$refs.tableColumnFilterDialog.setDialogVisible(false);
    },
    // 处理表格列过滤显示
    initColumns: function() {
      this.columns = [
        {
          prop: "V_PAY_NAME",
          label: "账户名",
          minWidth: 120
        },
        {
          prop: "V_TOTAL_MONEY",
          label: "总收款",
          minWidth: 120
        },
        {
          prop: "V_ALI_MONEY",
          label: "支付宝收款",
          minWidth: 120
        },
        {
          prop: "V_WX_MONEY",
          label: "微信收款",
          minWidth: 120
        }
      ];
      this.filterColumns = JSON.parse(JSON.stringify(this.columns));
    },
    handleAvatarSuccess(res, file) {
      this.dataForm.V_FILE_ID = res.result.ID;
      this.imageUrl = URL.createObjectURL(file.raw);
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === "image/jpeg";
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        this.$message.error("上传头像图片只能是 JPG 格式!");
      }
      if (!isLt2M) {
        this.$message.error("上传头像图片大小不能超过 2MB!");
      }
      return isJPG && isLt2M;
    },
    accountMatch: function(data) {
      let params = {
        ID:data.ID,
        V_IS_MATCH:data.V_IS_MATCH
      }
      this.settingAccount(params)
    },
    settingAccount:function(params){
      this.$api.account
          .accountConf(params)
          .then(res => {
            this.editLoading = false;
            if (res.success) {
  
            } else {
              this.$message({
                message: "操作失败, " + res.msg,
                type: "error"
              });
            }
            this.findPage(null);
          })
          .catch(res => {
            this.editLoading = false;
            this.$message({
              message: res.message,
              type: "error"
            });
          });
    },
  },
  mounted() {
    this.findPage(null);
    this.initColumns();
  }
};
</script>

<style scoped>
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
</style>


